.profile__avatar {
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 30px;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .profile__avatar {
        align-self: center;
        margin-right: 0;
        margin-bottom: 22.25px;
    }
}