.popup__container {
    max-width: 430px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 25px rgba(0, 0, 0, .15);
    box-sizing: border-box;
    position: relative;
}

@media screen and (max-width: 768px) {
    .popup__container {
        max-width: 282px;
    }
}